<template>
  <div class="user">
    <!--  -->
    <el-header>
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home/homepage' }"
          ></el-breadcrumb-item
        >
        <el-breadcrumb-item></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
      <el-dropdown>
        <span>
          <img
            src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=3018968254,2801372361&fm=26&gp=0.jpg"
            alt=""
          />
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item></el-dropdown-item>
          <el-dropdown-item></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-header>
    <!--  -->
    <div class="cartlist">
      <el-table :data="tableData" style="width: 100%" border>
        <el-table-column type="expand">
          <template slot-scope="props">
            <el-form label-position="left" inline class="demo-table-expand">
              <el-form-item label="">
                <span>{{ props.row.name }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.address }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.description }}</span>
              </el-form-item>
              <el-form-item label=" ID">
                <span>{{ props.row.id }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.phone }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.rating }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.recent_order_num }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.category }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column prop="name" label=""> </el-table-column>
        <el-table-column prop="address" label=""> </el-table-column>
        <el-table-column prop="description" label=""> </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="mini" @click="editCart(scope.row)"></el-button>
            <el-button size="mini" @click="$router.push('/home/addshoplist')"></el-button>
            <el-button size="mini" type="danger" @click="delData()"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="cartList.latitu"
        :page-size="cartList.offset"
        layout="total, prev, pager, next"
        :total="total"
      >
      </el-pagination>
      <!--  -->
      <el-dialog title="" :visible.sync="Editdialog" width="45%">
        <el-form ref="form" label-width="80px">
          <el-form-item label="">
            <el-input v-model="username"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addre"></el-input>
            <span></span>
          </el-form-item>
          <el-form-item label="" width="100">
            <el-input v-model="introduce"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="call"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-cascader :options="options"></el-cascader>
          </el-form-item>
          <el-form-item label="">
            
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="Editdialog = false"> </el-button>
          <el-button type="primary" @click="editCartList('editCartForm')"
            > </el-button
          >
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { getShopCount } from "../api/userList";
export default {
  data() {
    return {
      tableData: [],
      cartList: {
        latitu: 1,
        offset: 0,
        limit: 10,
      },
      Editdialog: false,
      id: "",
      username: "",
      addre: "",
      introduce: "",
      call: "",
      image:'',
      // editList: [],
      options: [
        {
          value: "zhinan",
          label: "",
          children: [
            {
              value: "shejiyuanze",
              label: "",
            },
            {
              value: "daohang",
              label: "",
            },
          ],
        },
        {
          value: "zujian",
          label: "",
          children: [
            {
              value: "basic",
              label: "Basic",
            },
            {
              value: "form",
              label: "Form",
            },
            {
              value: "notice",
              label: "Notice",
            },
            {
              value: "navigation",
              label: "Navigation",
            },
            {
              value: "others",
              label: "Others",
            },
          ],
        },
        {
          value: "ziyuan",
          label: "",
          children: [
            {
              value: "axure",
              label: "Axure Components",
            },
            {
              value: "sketch",
              label: "Sketch Templates",
            },
            {
              value: "jiaohu",
              label: "",
            },
          ],
        },
      ],
      imageUrl: "",
      total: 0,
      fits: ["fill", "contain", "cover", "none", "scale-down"],
    };
  },
  created() {},
  mounted() {
    // this.UserShopList();
    this.initData();
  },
  methods: {
    // 
    async UserShopList() {
      let { data: res } = await this.$http.get(
        `/shopping/restaurants?latitude=39.90469&longitude=116.407173&offset=${this.cartList.offset}&limit=${this.cartList.limit}`
      );
      // console.log(res);
      this.tableData = res;
    },
    // 
    editCart(arr) {
      this.Editdialog = true;
      this.id = arr.id;
      this.username = arr.name;
      this.introduce = arr.description;
      this.addre = arr.address;
      this.call = arr.phone;
      this.image = arr.image_path
    },
    // 
    async editCartList() {
      let { data: res } = await this.$http.post(
        `/shopping/updateshop?id=${this.id}`,
        {
          name: this.username,
          address: this.addre,
          description: this.introduce,
          phone: this.call,
          image_path:this.image
        }
      );
      console.log(res);
      this.Editdialog = false;
      this.UserShopList();
    },
    // 
    handleSizeChange(size) {
      console.log(`${size}`);
    },
    handleCurrentChange(page) {
      this.cartList.latitu = page;
      this.cartList.offset = (page - 1) * this.cartList.limit;
      this.UserShopList();
    },
    // 
    async initData() {
      try {
        let { data: countData } = await getShopCount(this.offset, this.limit);
        // cosnole.log(countData)
        if (countData.status == 1) {
          this.total = countData.count;
        } else {
          console.log("");
        }
        this.UserShopList();
      } catch (err) {
        console.log("");
      }
    },
    // 
    delData(){
      this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });
    }
  },
};
</script>

<stylescoped>
.el-header {
  background-color: #eff2f7;
  color: #333;
  display: flex;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
}
.el-dropdown img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.cartlist {
  padding: 20px;
}
.demo-table-expand {
  font-size: 0;
}
.el-form-item__label {
  width: 90px;
  color: #99a9bf;
}
.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 50%;
}
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
