import request from '../utils/request'

// 
export function getUserCount(data){
    return request({
        url:'/v1/users/count',
        method:'GET',
        data:{data}
    })
}
// 
export function getShopCount(data){
    return request({
        url:`/shopping/restaurants/count`,
        method:'GET',
        data:{data}
    })
}
//
export function getFoodCount(data){
    return request({
        url:`/shopping/v2/foods/count?restaurant_id=undefined`,
        method:'GET',
        data:{data}
    })
} 

