<script>
export default {
  name: "",
  data() {
    return {
      formData: {
        is_premium: true,
        delivery_mode: true,
        new: true,
        bao: true,
        zhun: true,
        piao: true,
        value1: true,
        selectActivity: true,
        float_delivery_fee: 5, //
        float_minimum_order_amount: 20, //
        startTime: "",
        endTime: "",
        imageUrl: "",
        address: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: "",
        addlist:[]
      },
      selectedCategory: ["", ""],
      categoryOptions: [],
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        address: [
          { required: true, message: "", trigger: "blur" }
        ],
        phone: [
          { required: true, message: "" },
          { type: "number", message: "" }
        ]
      },
      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ],
      activityValue: "",
      activities: [
        {
          icon_name: "",
          name: "",
          description: "305608"
        }
      ]
    };
  },
  mounted() {
    this.getdatas();
  },
  computed: {},
  methods: {
    //JSON
    getdatas() {
      this.$axios.get("/shopping/v2/restaurant/category").then(res => {
        console.log(res);

        this.addlist = res.data;
      });
    },

    submitForm(formName){
      this.$refs[formName].validate(async valid=>{
        if(valid){

        }
      })
    },
    

    //
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(" JPG !");
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
      }
      return isJPG && isLt2M;
    }
  }
};
</script>

<style lang="scss" scoped>
.button_submit {
  text-align: center;
}

.avatar-uploader {
  width: 120px;
  border: 1px dashed #d9d9d980;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader:hover {
  border-color: #20a0ff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.avatar {
  width: 120px;
  height: 120px;
  display: block;
}
.el-table .info-row {
  background: #c9e5f5;
}

.el-table .positive-row {
